<template>
  <div id="app">
    <b-container>
      <b-jumbotron header="BootstrapVue" lead="Bootstrap v4 Components for Vue.js 2">
        <p>For more information visit our website</p>
        <b-btn variant="primary" href="https://bootstrap-vue.org/">More Info</b-btn>
      </b-jumbotron>

      <b-form-group
        horizontal
        :label-cols="4"
        description="Let us know your name."
        label="Enter your name"
      >
        <b-form-input v-model.trim="name"></b-form-input>
      </b-form-group>

      <b-alert variant="success" :show="showAlert">Hello {{ name }}</b-alert>
    </b-container>
  </div>
</template>

<script>
// https://bootstrap-vue.org/docs/reference/starter-templates#basic-example
export default {
  name: 'Demo',
  // data: {
  //   name: ''
  // },
  /*
  `data` property in component must be a function
  https://cn.vuejs.org/v2/guide/components.html#data-%E5%BF%85%E9%A1%BB%E6%98%AF%E5%87%BD%E6%95%B0
  */
  data: function() {
    return {
      name: ''
    };
  },
  computed: {
    showAlert() {
      return this.name.length > 4 ? true : false
    }
  }
}
</script>

